$buttons: (
  ".button, a.button, button, [type=submit], [type=reset], [type=button]"
) !default; // Stack of elements to target
$button-border-width: 1px !default;
$button-border-radius: get-size(0.5) !default;
$button-font-family: v(font-secondary) !default;
$button-padding-xsmall: 0.5em 0.75em !default;
$button-padding-small: 0.6em 1em !default;
$button-padding: 0.75em 1.25em !default;
$button-padding-large: 0.75em 1.5em !default;
$button-text-transform: none !default;
$button-font-weight: $weight-bold !default;
$button-font-size-xsmall: font-size(-2) !default;
$button-font-size-small: font-size(-1) !default;
$button-font-size: font-size(0) !default;
$button-font-size-large: font-size(1) !default;
$button-hover-value: 700 !default;
$button-disabled-opacity: 0.65 !default;
$button-list-button-margin: get-size(0.5) !default;
$button-base-color: primary !default;
// TODO: Look at setting min and max-width of buttons with ch (character height)
