/* TEXT TRANSFORM */
%small {
  font-size: font-size(-1);
}
small {
  display: block;
  @extend %small;
}

strong,
b {
  font-weight: $weight-bold;
}
em,
i {
  font-style: italic;
}

// Highlighted
mark {
  background: rgb(255, 255, 157);
  padding: 0.15em 0.3em;
  display: inline-block;
}

// Inserted text
ins {
  text-decoration: underline;
}

// Deleted text
del {
  text-decoration: line-through;
}

// Computer Output
samp {
  @extend %code;
}

// Abbreviation
abbr {
  cursor: help;
}

// Definition
dfn {
  font-style: italic;
}

// Citation
cite {
  font-style: italic;
}

// Inline Quote
q {
  font-style: italic;
  &::before,
  &::after {
    content: '"';
  }
}

%inline-code {
  @extend %code;
  font-size: font-size(-1);
  line-height: 1;
  border-radius: $border-radius;
  padding: 0.3em 0.35em;
}

// Keyboard Input
kbd {
  @extend %inline-code;
  background-color: v(color-text);
  color: $color-white;
}

// Code
:not(pre) > code {
  @extend %inline-code;
  background-color: get-color(neutral, 50);
  color: v(color-text);
}

// Variable
var {
  @extend %inline-code;
  background-color: get-color(neutral, 50);
  font-style: italic;
  color: get-color(neutral, 800);
}

// Subscript and Superscript
sub,
sup {
  font-size: font-size(-2);
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.75em;
}
